export default {
    props: {
        toggle: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            GuideShow: false
        }
    },
    watch: {
        toggle() {
            this.GuideShow = !this.GuideShow;
        }
    },
    methods: {
        stop() {
            console.log(111)
        },
        showLoading: function() {
            this.graceFullLoading = true;
            setTimeout(function() {
                _self.graceFullLoading = false;
            }, 2000);
        },
        // 打开引导
        showGuide: function() {
            this.GuideShow = true;
        },
        // 关闭引导
        closeGuide: function() {
            this.GuideShow = false;
            this.$emit('close')
        },
    }
}
